import React from 'react';

import './Skill.scss';

const Skill = props => {
    const language = props.language;
    const skill = props.skill;
    let icon;

    const openCloseSkillHandler = event => {
        const icon = event.target;

        icon.classList.toggle('fa-chevron-circle-down');
        icon.classList.toggle('fa-chevron-circle-up');
        icon.parentNode.parentNode.classList.toggle('open');
    }

    if(skill.icon) {
        icon = <i 
            className={skill.className} 
        >
        </i>;
    } else {
        icon = <div className="img__container">
            <img 
                src={skill.src} 
                alt={skill.alt} 
                className={skill.className} 
            />
        </div>;
    }

    return (
        <React.Fragment>
            {!props.isMobile &&
                <div className="skill__container"> 
                    <div className="skill__display">
                        <h2 className="skill__title">
                            {skill[language].title}
                        </h2>
                        <i 
                            className="fas fa-chevron-circle-down"
                            onClick={openCloseSkillHandler}
                        ></i>
                    </div>
                    <div className="skill__symbol-text">
                        <div className="skill__symbol-container">
                            <div className="skill__symbol">
                                {icon}
                            </div>
                        </div>
                        <div className="skill__text">
                            <ul className="skill__description">
                                {skill[language].description.map((p, i) => <li key={i}><i className="fas fa-check"></i> {p}</li>)}
                            </ul> 
                        </div>
                    </div>
                </div>
            }
            {props.isMobile && 
                <div className="skill__container-mobile">
                    <div className="skill__mobile-display">
                        <h2 className="skill__title">
                            {skill[language].title}
                        </h2>
                        <i 
                            className="fas fa-chevron-circle-down"
                            onClick={openCloseSkillHandler}
                        ></i>
                    </div>
                    <div className="skill__symbol-container">
                        <div className="skill__symbol">
                            {icon}
                        </div>
                    </div>
                    <div className="skill__text">
                        <ul className="skill__description">
                            {skill[language].description.map((p, i) => <li key={i}><i class="fas fa-check"></i> {p}</li>)}
                        </ul> 
                    </div>
                </div>
            }
        </React.Fragment>
    )
};

export default Skill;